@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("OpenSans-Regular.woff") format("woff2"),
    url("OpenSans-Regular.ttf") format("ttf"),
    url("OpenSans-Regular.eot") format("eot");
}

@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("OpenSans-Bold.woff") format("woff2"),
  url("OpenSans-Bold.ttf") format("ttf"),
  url("OpenSans-Bold.eot") format("eot");
}
